
:root{
    --header_height:100px;
    --padding_lr:10px;
    --color_theme:#95bb2d;
    --max_width:1340px;
}

.app_body_container{
    margin-top: calc( var(--header_height));
    /* position: relative; */
}
.app_body_content{
    max-width: var(--max_width);
    padding: 10px var(--padding_lr);
    margin: auto;
}
/**Header Component **/
.header_component{
    position: fixed;
    height: var(--header_height);
    width: 100%;
    top:0;
    left: 0;
    background-color: var(--color_theme);
    box-shadow: 0 0 20px rgb(0 0 0 / 25%);
    z-index: 99;
 
}

.header_content{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    max-width: var(--max_width);
    height: 100%;
    margin:auto;
    padding:0px var(--padding_lr)
}

.flex_row_container{
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    flex-wrap: nowrap;
    flex-direction: row;
    column-gap: 20px;
}

.pointer{
    cursor: pointer;
}

/**Header Component end**/

/**Footer Component start**/

.footer_component{
    position: relative;
    bottom: 0px;
    width: 100%;
    left: 0px;
    background-color: var(--color_theme);
}

.footer_container{
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: auto;
    max-width: var(--max_width);
    margin:auto;
    padding:10px var(--padding_lr) 10px;
    
}

.footer_grid_2{
    justify-self: right;
}
/**Footer Component End**/


/**Banner Container Start**/
.banner_component{
    margin:50px 0px;
}
.banner_container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    align-items: center;
}   

/**Banner Container End**/